    border-radius: 30px;
    padding: 0.7em 1.5em;
}


.loading-dots {
  text-align: center;
  z-index: 5;
}

.loading-dots .dot {
    display: inline;
    margin-left: 0.05em;
    margin-right: 0.05em;
    color: #55B561;
    position: relative;
    top: -1em;
    font-size: 3.5em;
    opacity: 0;
    -webkit-animation: showHideDot 2.5s ease-in-out infinite;
            animation: showHideDot 2.5s ease-in-out infinite;
  }

  .loading-dots .one {
      -webkit-animation-delay: 0.2s;
              animation-delay: 0.2s;
  }
  .loading-dots .two {
      -webkit-animation-delay: 0.4s;
              animation-delay: 0.4s;
  }
  .loading-dots .three{
      -webkit-animation-delay: 0.6s;
              animation-delay: 0.6s;
  }
  .loading-dots .four {
      -webkit-animation-delay: 0.8s;
              animation-delay: 0.8s;
  }
  .loading-dots .five{
      -webkit-animation-delay: 1s;
              animation-delay: 1s;
  }

@-webkit-keyframes showHideDot {
  0% { opacity: 0; }
  50% { opacity: 1; }
  60% { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes showHideDot {
  0% { opacity: 0; }
  50% { opacity: 1; }
  60% { opacity: 1; }
  100% { opacity: 0; }
}


